.btn {
    border-radius: 15px;
    color: #ffffff;
    border: none;
    padding: 5px;
    cursor: pointer;
}

.btn--primary {
    background-color: #ffa500;
}

.btn--secondary {
    background-color: rgb(80, 82, 84);
}

.btn--small {
    font-size: 14px;
}

.btn--medium {
    font-size: 18px;
}